		}



		footer {
			position: fixed;
			bottom: 0;
			left: 0;
			right: 0;
			background: var(--theme_bg_color);
			box-shadow: 0 -2px 12px var(--color-shadw-1);
			transition: all 0.6s;
			transform: translateY(100%);

